import React from 'react';
import { Link } from 'react-router-dom';
import { Helmet } from 'react-helmet';
export default function Student(props) {
    const data = props.data;
    return (
        <>
            <h1>Thng tin sinh vin</h1>
            <Helmet>
                <title>Thng tin sinh vin {data.name} | QLSV</title>
            </Helmet>
            <div className="form-group">
                <label>M sinh vin: </label>
                <span className='text-danger'>{data.id}</span>
            </div>
            <div className="form-group">
                <label>Tn: </label>
                <span className='text-primary'>{data.name}</span>
            </div>
            <div className="form-group">
                <label>Ngy sinh: </label>
                <span className='text-primary'>{data.birthday}</span>
            </div>
            <div className="form-group">
                <label>Gii tnh: </label>
                <span className='text-primary'>{data.gender}</span>
            </div>
            <div className="form-group">
                <Link to={`/student/edit/${data.id}`} className="btn btn-warning btn-sm" >Sa</Link>
            </div>

        </>
    );
}
