import React from 'react';
import { useFormik } from 'formik';
import * as Yup from 'yup';
export default function CreateStudentForm(props) {
    const handleCreate = props.handleCreate;
    const formik = useFormik({
        initialValues: {
            name: '',
            birthday: '',
            gender: '',
        },
        validationSchema: Yup.object({
            name: Yup.string()
                .matches(/^[a-zA-ZAabcdeghklmnopqrstuvxyABCDEGHIKLMNOPQRSTUVXY\s]+$/, 'Vui lng nhp ng nh dng h v tn')
                .max(255, 'Vui lng nhp khng qu 255 k t')
                .required('Vui lng nhp tn'),
            birthday: Yup.string()
                .required('Vui lng nhp ngy sinh'),
            gender: Yup.string().required('Vui lng chn gii tnh'),
        }),
        onSubmit: async (values) => {
            handleCreate(values);

        }
    });

    return (
        <form method="POST" onSubmit={formik.handleSubmit}>
            <div className="container">
                <div className="row">
                    <div className="col-md-5">
                        <div className="form-group">
                            <label>Tn</label>
                            <input type="text" className="form-control" placeholder="Tn ca bn" name="name" onChange={formik.handleChange}
                                onBlur={formik.handleBlur}
                                value={formik.values.name} />
                            {formik.touched.name && formik.errors.name ? (
                                <div className="text-danger">{formik.errors.name}</div>
                            ) : null}

                        </div>
                        <div className="form-group">
                            <label>Birthday</label>
                            <input type="date" className="form-control" placeholder="Ngy sinh ca bn" name="birthday" onChange={formik.handleChange}
                                onBlur={formik.handleBlur}
                                value={formik.values.birthday} />
                            {formik.touched.birthday && formik.errors.birthday ? (
                                <div className="text-danger">{formik.errors.birthday}</div>
                            ) : null}
                        </div>
                        <div className="form-group">
                            <label>Chn Gii tnh</label>
                            <select className="form-control" id="gender" name="gender" onChange={formik.handleChange}
                                onBlur={formik.handleBlur}
                                value={formik.values.gender}>
                                <option value="">---</option>
                                <option value="nam">Nam</option>
                                <option value="n">N</option>
                                <option value="khc">Khc</option>
                            </select>
                            {formik.touched.gender && formik.errors.gender ? (
                                <div className="text-danger">{formik.errors.gender}</div>
                            ) : null}
                        </div>
                        <div className="form-group">
                            <button className="btn btn-success" type="submit">Lu</button>
                        </div>
                    </div>
                </div>
            </div>
        </form>
    );
}
