
import React from 'react';
import { useEffect, useState, useRef } from 'react';
import Search from '../../component/Search';
import Loading from '../../component/Loading';
import { toast } from 'react-toastify';
import { useSearchParams, NavLink, useLocation } from "react-router-dom";
import { getAcessToken, updateParam } from '../../common/util';
import Pagination from '../../component/Pagination';
import StudentList from '../../component/StudentList';
import axios from 'axios';
import ConfirmationDialog from '../../component/ConfirmationDialog';
import { Helmet } from 'react-helmet';

export function Index() {
    const [searchParams, setSearchParams] = useSearchParams();
    // H tr  xa da vo id
    const idItemRef = useRef(null);
    // Tm kim
    const handleSearch = (e) => {
        e.preventDefault();
        const search = document.querySelector('[name=search]').value;
        setSearch(search);
        const newParams = {};
        newParams['search'] = search;
        newParams['page'] = 1;
        updateParam(searchParams, setSearchParams, newParams);
        // Reset current page
        setCurrentpage(1);
    }

    // Phn Trang
    const handlePage = (page) => {
        setCurrentpage(page);
        const newParams = {};
        newParams['page'] = page;
        updateParam(searchParams, setSearchParams, newParams);
    }

    //handleShowDialog
    const handleShowDialog = (id) => {
        setShowConfirmationDialog(true);
        idItemRef.current = id;
    }

    const handleCloseDialog = () => {
        setShowConfirmationDialog(false);
    }

    // Xa 
    const handleDestroy = async () => {
        const id = idItemRef.current;
        try {
            setShowConfirmationDialog(false);
            await axios.delete(
                `${process.env.REACT_APP_API_URL}/api/v1/students/${id}`,
                {
                    headers: {
                        'Authorization': `Bearer ${getAcessToken()}`
                    }
                }
            );
            toast.success(' xa mn hc thnh cng');
            getAPIData();

        }
        catch (error) {

            toast.error(error?.response?.data || error.message);
        };
    }

    // Ly mc nh t ng dn
    const defaultPage = Number(searchParams.get('page') || 1);
    const defaultSearch = searchParams.get('search') || '';

    const [students, setStudents] = useState([]);
    const [pagination, setPagination] = useState([page => 1, totalPage => 1]);
    const [totalItem, setTotalItem] = useState(0);
    const [isLoaded, setIsLoaded] = useState(false);
    const [currentPage, setCurrentpage] = useState(defaultPage);
    const [search, setSearch] = useState(defaultSearch);

    const [showConfirmationDialog, setShowConfirmationDialog] = useState(false);

    const location = useLocation();
    const locationSearch = location.search;

    // Ln u tin khi component c render
    // V chy li khi status change (cha dng ti)
    useEffect(() => {
        getAPIData();
        // disable dependency warning
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [currentPage, search]);

    // Fix li khi click li vo NavLink student ang active
    useEffect(() => {

        if (locationSearch === '') {
            setCurrentpage(1);
            setSearch('');
        }
        // disable dependency warning
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [locationSearch]);

    const getAPIData = async () => {
        try {
            let response = await axios.get(
                `${process.env.REACT_APP_API_URL}/api/v1/students?page=${currentPage}&search=${search}`, {
                headers: {
                    'Authorization': `Bearer ${getAcessToken()}`
                }
            }
            );
            let data = response.data;
            setStudents(data.items);
            setTotalItem(data.totalItem);
            setPagination(data.pagination);
            setIsLoaded(true);

        }
        catch (error) {
            setIsLoaded(true);
            toast.error(error?.response?.data || error.message);
        };
    }
    return (
        <>
            <h1>Danh sch sinh vin</h1>
            <Helmet>
                <title>Danh sch sinh vin | QLSV</title>
            </Helmet>
            <NavLink to="/student/create" className="btn btn-info">Add</NavLink>
            <Search handleSearch={(e) => handleSearch(e)} search={search} />
            {
                !isLoaded ?
                    <Loading />
                    :
                    <>
                        <StudentList students={students} handleShowDialog={handleShowDialog} />
                        <div>
                            <span>S lng: {students.length}/{totalItem}</span>
                        </div>

                        <Pagination pagination={pagination} currentPage={currentPage} handlePage={handlePage} />

                        {/* confirm delete */}
                        {showConfirmationDialog && (
                            <ConfirmationDialog
                                message="Bn c mun xa mc ny?"
                                onConfirm={handleDestroy}
                                onCancel={handleCloseDialog}
                            />
                        )}
                    </>
            }

        </>
    );
}

