import React from 'react';
import { useFormik } from 'formik';
import * as Yup from 'yup';
export default function CreateStudentForm(props) {
    const handleCreate = props.handleCreate;
    const formik = useFormik({
        initialValues: {
            name: '',
            number_of_credit: '',
        },
        validationSchema: Yup.object({
            name: Yup.string()
                .max(255, 'Vui lng nhp khng qu 255 k t')
                .required('Vui lng nhp tn'),
            number_of_credit: Yup.number()
                .typeError('Vui lng nhp s')
                .integer('Vui lng nhp s nguyn')
                .min(1, 'Vui lng nhp s ln hoc hoc bng 1')
                .max(10, 'Vui lng nhp s nh hoc hoc bng 10')
                .required('Vui lng nhp s tn ch'),

        }),
        onSubmit: async (values) => {
            handleCreate(values);

        }
    });

    return (
        <form method="POST" onSubmit={formik.handleSubmit}>
            <div className="container">
                <div className="row">
                    <div className="col-md-5">
                        <div className="form-group">
                            <label>Tn</label>
                            <input type="text" className="form-control" placeholder="Tn mn hc mi" name="name" onChange={formik.handleChange}
                                onBlur={formik.handleBlur}
                                value={formik.values.name} />
                            {formik.touched.name && formik.errors.name ? (
                                <div className="text-danger">{formik.errors.name}</div>
                            ) : null}

                        </div>
                        <div className="form-group">
                            <label>S tn ch</label>
                            <input type="text" className="form-control" placeholder="S tn ch" name="number_of_credit" onChange={formik.handleChange}
                                onBlur={formik.handleBlur}
                                value={formik.values.number_of_credit} />
                            {formik.touched.number_of_credit && formik.errors.number_of_credit ? (
                                <div className="text-danger">{formik.errors.number_of_credit}</div>
                            ) : null}
                        </div>

                        <div className="form-group">
                            <button className="btn btn-success" type="submit">Lu</button>
                        </div>
                    </div>
                </div>
            </div>
        </form>
    );
}
