import React from 'react';
import { Link } from 'react-router-dom';
import { Helmet } from 'react-helmet';
export default function Subject(props) {
    const data = props.data;
    return (
        <>
            <h1>Thng tin mn hc</h1>
            <Helmet>
                <title>Thng tin mn hc {data.name} | QLSV</title>
            </Helmet>
            <div className="form-group">
                <label>M mn hc: </label>
                <span className='text-danger'>{data.id}</span>
            </div>
            <div className="form-group">
                <label>Tn: </label>
                <span className='text-primary'>{data.name}</span>
            </div>
            <div className="form-group">
                <label>S tn ch: </label>
                <span className='text-primary'>{data.number_of_credit}</span>
            </div>

            <div className="form-group">
                <Link to={`/subject/edit/${data.id}`} className="btn btn-warning btn-sm" >Sa</Link>
            </div>

        </>
    );
}
