import Footer from './Footer';
export default function Layout() {
    // Lu  Header v Footer ch render 1 ln duy nht
    return (
        <div className="container" style={{ marginTop: 20 }}>
            <Header />
            <Outlet />
            <Footer />
        </div>

    );
}
