import React from 'react';
import { useState, useEffect } from 'react';

export default function Search(props) {
    // Phi s dng state v useEffect  cp nht value ca th input bng vi gi tr ca props.search
    const [pattern, setPattern] = useState(props.search);

    // props.search thay i th chy code trong useEffect
    useEffect(() => {
        setPattern(props.search);
    }, [props.search]);
    return (
        <>
            <form action="#" method="GET" onSubmit={props.handleSearch}>
                <label className="form-inline justify-content-end">Tm kim: <input type="search" name="search" className="form-control" onChange={e => setPattern(e.target.value)} value={pattern} />
                    <button className="btn btn-danger" >Tm</button>
                </label>
            </form>
        </>
    );
}

