import React from 'react';
import { useFormik } from 'formik';
import * as Yup from 'yup';
import { Helmet } from 'react-helmet';
export default function EditRegisterForm(props) {
    const data = props.data;
    const handleUpdate = props.handleUpdate;
    const formik = useFormik({
        initialValues: data,
        validationSchema: Yup.object({
            score: Yup.number().typeError('Vui lng nhp s')
                .min(0, 'Vui lng nhp im ln hn hoc bng 0')
                .max(10, 'Vui lng nhp im nh hn hoc bng 10')
                .nullable()
        }),
        onSubmit: async (values) => {

            handleUpdate(values);
        }
    });

    return (
        <>
            <Helmet>
                <title>Cp nht im | QLSV</title>
            </Helmet>
            <form action="" method="POST" onSubmit={formik.handleSubmit}>
                <div className="container">
                    <div className="row">
                        <div className="col-md-5">
                            <div className="form-group">
                                <label>Tn sinh vin: </label>
                                <span>{data.student_name}</span>
                            </div>
                            <div className="form-group">
                                <label>Tn mn hc: </label>
                                <span>{data.subject_name}</span>
                            </div>
                            <div className="form-group">
                                <label htmlFor="score">im</label>
                                <input type="text" name="score" id="score" onChange={formik.handleChange}
                                    onBlur={formik.handleBlur}
                                    value={formik.values.score} />
                                {formik.touched.score && formik.errors.score ? (
                                    <div className="text-danger">{formik.errors.score}</div>
                                ) : null}
                            </div>
                            <div className="form-group">
                                <button className="btn btn-success" type="submit">Lu</button>
                            </div>
                        </div>
                    </div>
                </div>
            </form>

        </>

    );
}
